:root{
	--mainColor:#000000;
	
	--secondaryColor:#302b2b;

	--borderColor:#c1c1c1;

	--mainText:#FFFF00;
	--secondaryText:#eeeeee;


	--previewBg:rgba(255, 255, 255, 0.8);
	--previewShadow:#db7575;


	--buttonColor:black;


}


html, body{
	padding: 0;
	margin: 0;
	scroll-behavior: smooth;
}

body *{
	transition: 0.3s;
}


h1, h2, h3, h4, h5, h6, strong{
	color: var(--mainText);
	font-family: 'Russo One', sans-serif;
	font-weight: 500;
}

p, li, span, label, input, textarea{
	color: var(--secondaryText);
	font-family: 'Roboto Mono', monospace;
}

a{
	text-decoration: none;
	color:#17a2b8;
}

ul{
	list-style: none;
}

h1 { font-size: 56px;}
h2 { font-size: 36px;}
h3 { font-size: 28px;}
h4 { font-size: 24px;}
h5 { font-size: 20px;}
h6 { font-size: 16px;}


.s1{
	background-color: var(--mainColor);
	border-bottom:1px solid var(--borderColor);
	overflow:auto;
}

.s2{
	background-color: var(--secondaryColor);
	border-bottom:1px solid var(--borderColor);
	overflow:auto;
}




.main-container{
	width: 1380px;
	margin: 0 auto;
	background: url("images/bg3.jpg") no-repeat;
	
}


.greeting-wrapper{
	display: grid;
	text-align: center;
	align-content: center;
	min-height: 10em;
  height: 200px;

}

.intro-wrapper{
	background-color: var(--secondaryColor);
	border:1px solid var(--borderColor);
	border-radius:5px 5px 0 0;
	background: url("images/bg9.gif") no-repeat bottom;


	-webkit-box-shadow: -1px 1px 3px -1px rgba(0,0,0,0.75);
	-moz-box-shadow: -1px 1px 3px -1px rgba(0,0,0,0.75);
	box-shadow: -1px 1px 3px -1px rgba(0,0,0,0.75);

	display: grid;
	grid-template-columns: 1fr 1fr;
	grid-template-areas: 
		'nav-wrapper nav-wrapper'
		'left-column right-column'
	;

}
.intro-wrapper2{
	background-color: var(--secondaryColor);
	border:1px solid var(--borderColor);
	border-radius:5px 5px 0 0;
	background: url("images/bg4.jpeg") no-repeat;
	align-items: center;
	
	-webkit-box-shadow: -1px 1px 3px -1px rgba(0,0,0,0.75);
	-moz-box-shadow: -1px 1px 3px -1px rgba(0,0,0,0.75);
	box-shadow: -1px 1px 3px -1px rgba(0,0,0,0.75);

	display: grid;
	grid-template-columns: 1fr 1fr;
	grid-template-areas: 
		'nav-wrapper nav-wrapper'
		'left-column right-column'
	;

}


.nav-wrapper{
	border-radius:5px 5px 0 0;
	grid-area:nav-wrapper;
	border-bottom: 1px solid var(--borderColor);
	display: flex;
	justify-content: space-between;
	align-items: center;
	background-color: var(--mainColor);
	padding-left: 50px;
	width: 1329px;
	/* padding-right: 50px; */
}

#navigation a{
	color:var(--mainText);
}


#navigation{
	margin:0;
	padding: 10px;
	
}

#navigation li{
	display: inline-block;
	margin-right: 5px;
	margin-left:5px;
}

#navigation li .aa:hover{
	display: inline-block;
	margin-right: 5px;
	margin-left:5px;
  color: white;
}



.left-column{
	grid-area: right-column;
	padding-top:50px;
	padding-bottom: 50px;
  padding-right: 580px;
}

#profile_pic{
	display: block;
	margin:0 auto;

	height: 200px;
	width: 200px;
	object-fit: cover;
	border:2px solid var(--borderColor);
}


#settings-note{
	font-size: 12px;
	font-style: italic;
	text-align: center;
	font-family:cursive;
}

.right-column{
	grid-area: right-column;
	display: grid;
	align-content: center;

	padding-top: 50px;
	padding-bottom: 50px;
}


.about-wrapper{
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
	padding-bottom: 50px;
	padding-top: 50px;
	padding-left: 50px;
	padding-right: 50px;
	gap:100px;
	background: url("images/bg9.gif") no-repeat bottom;
}


#skills{
	display: flex;
	justify-content: space-evenly;
	/* background-color: var(--previewShadow); */
	background-color: rgba( 255,255, 255, 0.2);
	background-color: g;
  color: black;
}

.social-links{
	display: grid;
	align-content: center;
	text-align: center;
	/* background-image: url("images/31.jpeg"); */
}

#social_img{
	width: 100%;
}


.post-wrapper{
	
	display: grid;
	grid-template-columns: repeat(auto-fit, 320px);
	gap:20px;
	justify-content: center;
	padding-top: 50px;
	padding-bottom: 50px;
	background: url("images/bg9.gif") no-repeat bottom;
}


.post{
	border:1px solid var(--borderColor);
	-webkit-box-shadow: -2px 7px 21px -9px rgba(0,0,0,0.75);
	-moz-box-shadow: -2px 7px 21px -9px rgba(0,0,0,0.75);
	box-shadow: -2px 7px 21px -9px rgba(0,0,0,0.75);
	
}
.post:hover{
	border:1px solid var(--borderColor);
	-webkit-box-shadow: -2px 7px 21px -9px rgba(0,0,0,0.75);
	-moz-box-shadow: -2px 7px 21px -9px rgba(0,0,0,0.75);
	box-shadow: -2px 7px 21px -9px rgba(0,0,0,0.75);
  transform: scale(1.1);
}

.thumbnail{
	display: block;
	width: 100%;
	height:180px;
	object-fit: cover;
}

.post-preview{
	background-color: #fff;
	padding:15px;
	
}

.post-title{
	color:black;
	margin: 0;
}

.post-intro{
	color:#4b5156;
	font-size: 14px;
}


footer{
  display: flex;
  background: #010525;
  padding: 15px 23px;
  color: #fff;
  text-align: center;
  justify-content: space-around;
  font-family: 'Russo One', sans-serif;
}

footer span{
  color: #FFFF00;
  text-decoration: none;
  font-weight: 1000;
  font-family: 'Russo One', sans-serif;
  text-align: center;
}

footer .footer-bar .icon li {
  list-style: none;
  display: inline-block;
  text-align:justify;
  padding-right: 20px;
}

footer .footer-bar .icon li .f-button {
  color:#fff;
}
footer .footer-bar .icon li .f-button:hover {
  color:rgb(229, 255, 0);
}

@media screen and (max-width: 1200px){
	.main-container{
		width: 95%;
	}
}

@media screen and (max-width: 800px){
	.intro-wrapper{
		grid-template-columns: 1fr;
		grid-template-areas: 
			'nav-wrapper'
			'left-column'
			'right-column'
		;
	}

	.right-column{
		justify-content: center;
	}

}

@media screen and (max-width: 400px){
	#preview-shadow{
		max-width: 280px;
		height: 180px;
		padding-left: 10px;
		padding-top: 10px;
	}

	#preview{
		width: 280px;

	}

}